import { getAllByTestId } from '@testing-library/react';
import React, { useEffect, useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';
import './detail.scss';

let 박스 = styled.div`
    padding-top : 30px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상}
`;

function Detail(props){

    let [alert,alertChe] = useState(true);
    let [inputData,inputDataChe] = useState();

    useEffect(()=>{
        console.log('asdfkljhnaweF');
        let timer = setTimeout(() => { alertChe(false) }, 2000);
        console.log('안녕');
        return ()=>{clearTimeout(timer)}
    },[]);

    let { id } = useParams();
    let histroy = useHistory();
    let pindProduct = props.shoes.find(x => x.id == id);

    function invenDelete(){
        var invenArr = [...props.inventory];
        invenArr[id]--;
        if(invenArr[id] === 0){
            invenArr[id]++;
        }
        props.inventoryChe(invenArr);
    }

    return(
        <div className="container">
            <박스>
                <제목 className='red'>detail</제목>
            </박스>
            <input onChange={ (e)=>{inputDataChe(e.target.value) } } />
            {inputData}
            {
                alert === true
                ? <div className='alert_2'> 
                    <p>재고가 얼마남지 않았습니다. </p>
                </div>
                : null
            } 
            <div className="row">
                <div className="col-md-6">
                    <img src='https://codingapple1.github.io/shop/shoes1.jpg' width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">상품명 { pindProduct.title }</h4>
                    <p>상품설명 { pindProduct.content }</p>
                    <p> { pindProduct.price }</p>

                    <Info inventory={props.inventory[id]}></Info>
                    <button className="btn btn-danger" onClick={invenDelete}>주문하기</button> 
                    <button className="btn btn-danger" onClick={ ()=>{histroy.goBack();} }>뒤로가기</button> 
                </div>
            </div>
        </div> 
    )
}
  
function Info(props){
    return(
        <p>재고 : {props.inventory}</p>
    )
}
export default Detail;

